<template>
  <div class="class_list">

    <div class="sub_nav">
      <span>班级</span>
      <van-icon name="plus" @click="$router.push('/class/edit')"></van-icon>
    </div>

    <van-pull-refresh v-model="isLoading" @refresh="onSearch" class="pull_fresh">
      <van-cell-group v-if="classList && classList.length>0">
        <van-cell class="cell_main" is-link v-for="(item, index) in classList" :key="index" :title="item.name"
                  :label="'学生：'+item.person" @click="classClick(item)">
        </van-cell>
      </van-cell-group>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>

    <van-popup v-model="classOperate" position="bottom">
      <van-cell value="选择课时开始授课" @click.stop.prevent="toBegin">
        <van-icon slot="right-icon" name="other-pay"></van-icon>
      </van-cell>
      <van-cell value="学生列表" @click.stop.prevent="toStudent">
        <van-icon slot="right-icon" name="manager-o"></van-icon>
      </van-cell>
      <van-cell value="编辑" @click.stop.prevent="toEdit">
        <van-icon slot="right-icon" name="edit"></van-icon>
      </van-cell>
      <van-cell value="删除" @click.stop.prevent="toDrop">
        <van-icon slot="right-icon" name="delete"></van-icon>
      </van-cell>
    </van-popup>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { formatDate } from "@/utils/formatData";

export default {
  name: "classList",
  data () {
    return {
      subjectID: this.$store.getters.subjectID,
      classList: [],
      classOperate: false,
      classSelected: null,

      isLoading: false
    };
  },
  mounted () {
    this.getClassList();
  },
  methods: {
    onSearch () {
      this.getClassList();
    },
    getClassList () {
      this.classList = [];
      this.isLoading = true;
      this.$mainHttp.class.dataList({
        courseID: this.subjectID
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            this.classList.push({
              name: item.Key["ClassName"],
              createTime: formatDate(item.Key["CreateTime"], "yyyy-MM-dd"),
              code: item.Key["InviteCode"],
              person: item.Total,
              id: item.Key["ClassID"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.$emit("finish", this.classList);
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    classClick (item) {
      this.classSelected = item;
      this.classOperate = true;
    },
    editClass (item) {
      this.$router.push('/class/edit/' + item.id + "/" + item.name);
    },
    toBegin () {
      this.classOperate = false;
      this.$emit("toBegin", this.classSelected);
    },
    toStudent () {
      this.$store.commit("SET_CLASS", this.classSelected);
      this.$store.commit("SET_CLASS_ID", this.classSelected.id);
      this.$router.push("/class/student/list");
    },
    toEdit () {
      this.$router.push("/class/edit/" + this.classSelected.id + "/" + this.classSelected.name);
    },
    toDrop () {
      Dialog.confirm({
        message: "确认删除此班级？"
      }).then(() => {
        this.toDelete();
      }).catch(() => {
      });
    },
    toDelete () {
      this.$mainHttp.class.dataDelete({
        classID: this.classSelected.id,
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result) {
          this.classOperate = false;
          this.getClassList();
          this.$toast("删除成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("删除失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>
