<template>
  <div class="class_hour_list">
    <div class="sub_nav">
      <span>课时</span>
      <van-icon name="plus" @click="toAdd"></van-icon>
    </div>
    <van-pull-refresh v-model="isLoading" @refresh="onSearch" class="pull_fresh">
      <van-cell-group v-if="classHourList && classHourList.length>0">
        <van-cell class="cell_main" v-for="(item, index) in classHourList" :key="index" :title="item.name"
                  :label="item.createTime" is-link @click="classHourClick(item)">
        </van-cell>
      </van-cell-group>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>

    <van-popup v-model="classHourOperate" position="bottom">
      <van-cell value="选择班级开始授课" @click.stop.prevent="toBegin">
        <van-icon slot="right-icon" name="other-pay"></van-icon>
      </van-cell>
      <van-cell value="备课" @click.stop.prevent="toPrepare">
        <van-icon slot="right-icon" name="todo-list-o"></van-icon>
      </van-cell>
      <van-cell value="课堂记录" @click.stop.prevent="toRecord">
        <van-icon slot="right-icon" name="todo-list-o"></van-icon>
      </van-cell>
      <van-cell value="编辑" @click.stop.prevent="toEdit">
        <van-icon slot="right-icon" name="edit"></van-icon>
      </van-cell>
      <van-cell value="删除" @click.stop.prevent="toDrop">
        <van-icon slot="right-icon" name="delete"></van-icon>
      </van-cell>
    </van-popup>
  </div>
</template>

<script>
import { Dialog } from "vant";
import { formatDate } from "@/utils/formatData";

export default {
  name: "classHourList",
  data () {
    return {
      subjectID: this.$store.getters.subjectID,
      classHourList: [],
      classHourSelected: null,
      classHourOperate: false,
      isLoading: false
    };
  },
  mounted () {
    this.getClassHourList();
  },
  methods: {
    onSearch () {
      this.getClassHourList();
    },
    getClassHourList () {
      this.classHourList = [];
      this.isLoading = true;
      this.$mainHttp.classHour.dataList({
        courseID: this.subjectID
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            this.classHourList.push({
              name: item["CHName"],
              createTime: formatDate(item["CreateTime"], "yyyy-MM-dd"),
              code: item["SignCode"],
              id: item["CHID"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.$emit("finish", this.classHourList);
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    classHourClick (item) {
      this.classHourSelected = item;
      this.classHourOperate = true;
    },
    toBegin () {
      this.classHourOperate = false;
      this.$emit("toBegin", this.classHourSelected);
    },
    toPrepare () {
      this.$store.commit("SET_CLASSHOUR", this.classHourSelected);
      this.$store.commit("SET_CLASSHOUR_ID", this.classHourSelected.id);
      this.$router.push("/class-hour/prepare");
    },
    toRecord () {
      this.$store.commit("SET_CLASSHOUR", this.classHourSelected);
      this.$store.commit("SET_CLASSHOUR_ID", this.classHourSelected.id);
      this.$router.push("/course-record/list");
    },
    toAdd () {
      this.$store.commit("SET_CLASSHOUR", null);
      this.$store.commit("SET_CLASSHOUR_ID", "");
      this.$router.push('/class-hour/edit');
    },
    toEdit () {
      this.$store.commit("SET_CLASSHOUR", this.classHourSelected);
      this.$store.commit("SET_CLASSHOUR_ID", this.classHourSelected.id);
      this.$router.push('/class-hour/edit');
    },
    toDrop () {
      Dialog.confirm({
        message: "确认删除当前课时？"
      }).then(() => {
        this.toDelete();
      }).catch(() => {
      });
    },
    toDelete () {
      this.$mainHttp.classHour.dataDelete({
        chID: this.classHourSelected.id,
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result) {
          this.classHourOperate = false;
          this.getClassHourList();
          this.$toast("删除成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("删除失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>
