<template>
  <div class="subject_view">
    <head-top :title="title">
       <span slot="right" class="head-right" @click="dataDrop">
         <van-icon name="delete"/>
       </span>
    </head-top>

    <class-hour-list @finish="classHourChange" @toBegin="classHourBegin"></class-hour-list>

    <class-list @finish="classChange" @toBegin="classBegin"></class-list>

    <van-popup v-model="showPicker" position="bottom">
      <van-picker :title="pickerTitle" show-toolbar :columns="columns" @cancel="showPicker = false"
                  @confirm="onConfirm">
      </van-picker>
    </van-popup>
  </div>
</template>

<script>
import ClassList from "../class/components/ClassList";
import ClassHourList from "../class-hour/list";
import { Dialog } from "vant";

export default {
  name: "subjectView",
  components: { ClassList, ClassHourList },
  data () {
    return {
      title: this.$store.getters.subject.name,
      subjectID: this.$store.getters.subjectID,

      classHourList: [],
      classList: [],
      showPicker: false,
      pickerTitle: "",
      columns: [],
      classHourSelected: null,
      classSelected: null,
      pickerType: 1 // 1选择班级 2选择课时
    };
  },
  mounted () {
  },
  methods: {
    dataDrop () {
      Dialog.confirm({
        message: "确认删除当前课程？"
      }).then(() => {
        this.dataDelete();
      }).catch(() => {
      });
    },
    dataDelete () {
      this.$mainHttp.course.dataDelete({
        courseID: this.subjectID,
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result) {
          this.$toast("删除成功");
          this.$router.replace("/subject/list");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("删除失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },

    classHourChange (value) {
      this.classHourList.length = 0;
      if (value && value.length > 0) this.classHourList = value.slice(0, value.length);
    },
    classHourBegin (value) {
      this.pickerTitle = "请选择授课班级";
      this.classHourSelected = value;
      this.classSelected = null;
      this.columns = [];
      this.pickerType = 1;
      this.classList.forEach(item => {
        this.columns.push({
          text: item.name,
          value: item
        });
      });
      this.showPicker = true;
    },
    classChange (value) {
      this.classList.length = 0;
      if (value && value.length > 0) this.classList = value.slice(0, value.length);
    },
    classBegin (value) {
      this.pickerTitle = "请选择授课课时";
      this.classHourSelected = null;
      this.classSelected = value;
      this.columns = [];
      this.pickerType = 2;
      this.classHourList.forEach(item => {
        this.columns.push({
          text: item.name,
          value: item
        });
      });
      this.showPicker = true;
    },
    onConfirm (selected) {
      if (this.pickerType === 1) {
        this.classSelected = selected.value;
      } else {
        this.classHourSelected = selected.value;
      }
      this.showPicker = false;
      this.$mainHttp.classInfo.inClassInfo({
        chID: this.classHourSelected.id,
        classID: this.classSelected.id,
        teacherID: this.$store.getters.teacherID
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          const course = {
            id: res.Datas[0].Key["CIID"],
            classHourID: res.Datas[0].Key["CHID"],
            classID: res.Datas[0].Key["ClassID"],
            classRoomID: res.Datas[0].Key["ClassroomID"],
            classRoomName: res.Datas[0]["ClsRoomName"],
            teacherID: res.Datas[0].Key["TeacherID"],
            classDate: res.Datas[0].Key["ClassDate"],
            state: res.Datas[0].Key["State"],
            createTime: res.Datas[0].Key["CreateTime"],
            updateTime: res.Datas[0].Key["UpdateTime"]
          };

          this.$store.commit("SET_CLASS", this.classSelected);
          this.$store.commit("SET_CLASS_ID", this.classSelected.id);
          this.$store.commit("SET_CLASSHOUR", this.classHourSelected);
          this.$store.commit("SET_CLASSHOUR_ID", this.classHourSelected.id);

          this.$store.commit("SET_COURSE", course);
          this.$store.commit("SET_COURSE_ID", course.id);
          this.toStartCourse();
          // // 需默认禁止状态
          // this.$store.commit("SET_SCREEN_STATE", 0);
          // this.$store.commit("SET_QUESTION_STATE", 0);
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.toLinkCourse();
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    toStartCourse () {
      this.$store.dispatch("GetCourseServiceByUser", this.$store.getters.course.classRoomID).then(res => {
        if (res.Result) {
          this.linkTest();
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("获取教室服务失败");
        }
      }).catch(() => {
        this.$toast("获取教室服务出错");
      });
    },
    // 连接测试
    linkTest () {
      this.$gatewayHttp.gateway.linkTest().then(res => {
        this.$router.push("/course");
      }).catch(() => {
        this.toLinkCourse();
      });
    },
    toLinkCourse () {
      this.$mainHttp.resourse.signalRPort().then(res => {
        if (res.Result && res["Port"]) {
          this.$store.commit("SET_SIGNALR_PORT", res["Port"]);
          this.$store.commit("SET_CLASS", this.classSelected);
          this.$store.commit("SET_CLASS_ID", this.classSelected.id);
          this.$store.commit("SET_CLASSHOUR", this.classHourSelected);
          this.$store.commit("SET_CLASSHOUR_ID", this.classHourSelected.id);
          this.$router.push("/class-hour/wait");
          // this.$toast("开始授课失败");
        } else if (res.Info) {
          this.$toast(res.info);
        } else {
          this.$toast("请重试");
        }
      }).catch(() => {
        this.$toast("无法获取连接信息");
      });
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";

.subject_view {
  background-color: #fff;
  min-height: 100%;

  /deep/ .van-cell {
    align-items: center;
  }
}
</style>
